import React from 'react';
import { injectIntl } from 'react-intl';
import Page from 'material-ui-shell/lib/containers/Page/Page';
import Scrollbar from 'material-ui-shell/lib/components/Scrollbar';
import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Avatar,
  Typography,
  IconButton
} from '@material-ui/core';

import data from '../../data/msf';
import { openUrl } from '../../utils'

const { firstName, lastName, displayPicture, designation, description, socialNetworks } = data.about;
const fullName = `${firstName} ${lastName}`;

const renderActions = () => {
  return socialNetworks.map(({ name, url, icon }) => {
    return (
      <IconButton
        aria-label={name}
        key={name}
        onClick={() => openUrl(url)}
      >
        {icon}
      </IconButton>
    )
  });
}

const AboutPage = ({ intl }) => {
  return (
    <Page pageTitle={intl.formatMessage({ id: 'about' })}>
      <Scrollbar
        style={{ height: '100%', width: '100%', display: 'flex', flex: 1 }}
      >
        {/* {intl.formatMessage({ id: 'about' })} */}
        <Card>
          <CardHeader
            avatar={
              <Avatar
                src={displayPicture}
              />
            }
            title={fullName}
            subheader={designation}
          />
          <CardContent>
            <Typography variant="body2" color="textPrimary" component="p">
              {description}
            </Typography>
          </CardContent>
          <CardActions disableSpacing>
            {renderActions()}
          </CardActions>
        </Card>
      </Scrollbar>
    </Page>
  );
}

export default injectIntl(AboutPage);
